import React from "react";

import { Link } from "react-router-dom"


export default function Navbar() {
  return (
    <nav className="navbar navbar-expand-lg bg-success p-2 text-dark bg-opacity-50 ">
      <div className="container-fluid">
        <a className="navbar-brand" href="#"></a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link to="/Home" className="nav-link"> Home</Link>
            </li>
            <li className="nav-item">
              <Link to="/Tours" className="nav-link"> Tours</Link>
            </li>
            <li className="nav-item">
              <Link to="/Agency" className="nav-link">  About</Link>
            </li>
            <li className="nav-item">
              <Link to="/Wheather" className="nav-link"> Wheather</Link>
            </li>
            <li className="nav-item">
              <Link to="/Login" className="nav-link"> Login</Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  )
}
